* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  position: relative;
  background-image: url(/img/bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
@media (max-width: 599px) {
  body {
    background-position: -680px;
  }
}
body::after {
  z-index: -1;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.4));
}

.navbar {
  z-index: 999;
  position: sticky;
  top: 0;
  background: transparent;
  width: 100%;
  height: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar-logo {
  z-index: 9999;
  flex: 1;
  padding-left: 5rem;
  height: inherit;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .navbar-logo {
    padding-left: 2rem;
  }
}
.navbar-links {
  transition: all 0.3s ease-in-out;
  flex: 1;
  padding-right: 5rem;
  height: inherit;
}
.navbar-links-list {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-links-list a:hover {
  color: var(--secondary-colour);
}

.burger {
  z-index: 9999;
  height: inherit;
  padding-right: 5rem;
  display: flex;
  align-items: center;
}
@media (max-width: 599px) {
  .burger {
    padding-right: 2rem;
  }
}
.burger-container {
  height: 2rem;
  width: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  cursor: pointer;
}
.burger-container span {
  width: 2rem;
  height: 2px;
  background: var(--primary-colour);
  transform-origin: left;
  transition: all 0.3s ease-in-out;
}

#about {
  z-index: 999;
  height: 90vh;
  padding-top: 10rem;
}
@media (max-width: 599px) {
  #about {
    padding-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: flex-start;
  }
}
#about .about-me {
  width: 100%;
}
#about .about-me h2 {
  font-size: 2.8rem;
  padding-left: 5rem;
  padding-bottom: 2rem;
}
@media (max-width: 1399px) {
  #about .about-me h2 {
    font-size: 2.4rem;
  }
}
@media (max-width: 1023px) {
  #about .about-me h2 {
    font-size: 2rem;
  }
}
@media (max-width: 599px) {
  #about .about-me h2 {
    font-size: 1.4rem;
    padding-left: 2rem;
  }
}
#about .about-me h2 span {
  color: var(--secondary-colour);
}
#about .about-me h3 {
  font-size: 3.2rem;
  padding-left: 5rem;
  padding-right: 5rem;
}
@media (max-width: 1399px) {
  #about .about-me h3 {
    font-size: 3rem;
  }
}
@media (max-width: 1023px) {
  #about .about-me h3 {
    font-size: 2.4rem;
  }
}
@media (max-width: 599px) {
  #about .about-me h3 {
    padding-right: 2rem;
    padding-left: 2rem;
    font-size: 1.6rem;
  }
}
#about .about-me h3 span {
  color: var(--secondary-colour);
}

#projects {
  height: 90vh;
  padding-left: 5rem;
  padding-top: 5rem;
}
#projects .tip-calculator h2 {
  font-size: 2.2rem;
}
#projects .tip-calculator p {
  font-size: 1.5rem;
}

html {
  font-family: "Montserrat", sans-serif;
}

h1 {
  font-size: 2rem;
}

h2,
h3 {
  color: var(--primary-colour);
  letter-spacing: 2px;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: var(--primary-colour);
  font-weight: 700;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 599px) {
  a {
    font-size: 1.2rem;
  }
}

li {
  list-style: none;
}

p {
  color: var(--secondary-colour);
}

:root {
  --bg: #000000;
  --primary-colour: #29b58a;
  --secondary-colour: #b52954;
}

@media (max-width: 1023px) {
  .hide-for-mobile {
    background: var(--bg);
    position: absolute;
    top: 0%;
    left: 50%;
    transform: translatex(-50%);
    width: 0%;
    height: 60vh;
    padding: 3rem 0;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
  }
}
@media (max-width: 1023px) and (max-width: 599px) {
  .hide-for-mobile {
    height: 100vh;
    padding: 0;
  }
}
@media (max-width: 1023px) {
  .hide-for-mobile ul {
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
  }
}
@media (max-width: 1023px) and (max-width: 599px) {
  .hide-for-mobile ul {
    align-items: center;
  }
}

@media (min-width: 1024px) {
  .hide-for-desktop {
    display: none;
  }
}

.burger-animation span:nth-child(1) {
  transform: rotatez(45deg);
  background: var(--secondary-colour);
}
.burger-animation span:nth-child(2) {
  transform: translatex(200%);
  opacity: 0;
}
.burger-animation span:nth-child(3) {
  transform: rotatez(-45deg);
  background: var(--secondary-colour);
}

.show-menu {
  z-index: 1;
  opacity: 1;
  visibility: visible;
  width: 100vw;
}

.overflow {
  overflow: hidden;
}

.active {
  color: var(--secondary-colour);
}